h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
a,
span {
  font-size: 16px;
  color: #000;
  display: block;
  text-decoration: none;
  margin: 0px;
  padding: 0px;
  font-weight: 100; }

section.header h1 {
  text-align: center;
  padding: 100px 15px 0px;
  font-weight: 400;
  font-size: 30px;
  padding-bottom: 30px; }

section.header h3 {
  color: #999;
  text-align: center;
  font-weight: 400;
  font-size: 20px; }

section.header div.uploadBanner {
  background-color: #f1f1f1; }
  section.header div.uploadBanner ul {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    /* height: 100px; */
    margin: 60px auto;
    grid-gap: 20px;
    list-style-type: none;
    padding: 30px 10px;
    max-width: 700px; }
    @media (min-width: 600px) {
      section.header div.uploadBanner ul {
        grid-template-columns: auto auto;
        grid-template-rows: auto; } }
    section.header div.uploadBanner ul li div {
      display: flex;
      align-items: center;
      justify-content: center; }
    section.header div.uploadBanner ul li span {
      width: 322px;
      margin: 8px auto 0px;
      color: #d21903; }
    section.header div.uploadBanner ul li input[type='file'] {
      display: none; }
    section.header div.uploadBanner ul li label {
      border: 1px solid #ccc;
      display: inline-block;
      padding: 15px 20px;
      cursor: pointer;
      border-radius: 10px;
      width: 280px;
      text-align: center; }
      section.header div.uploadBanner ul li label:hover {
        background-color: #e1f789;
        border: 1px solid #999; }

section.header ul.shippingInfo {
  width: 500px;
  margin: 0px auto;
  border: 1px solid #f1f1f1;
  border-radius: 10px; }
  section.header ul.shippingInfo li {
    display: grid;
    grid-template-columns: 50px 150px 100px 100px 80px;
    padding: 10px; }
    section.header ul.shippingInfo li:first-child {
      background-color: #f1f1f1;
      border-radius: 10px 10px 0px 0px; }
